<template lang="html">
  <div class="bad-comment-page">
    <!-- 头部 -->
    <default-head header="差评待处理"></default-head>

    <white-title text="我收到的差评" class="borderB" :isShow="true" @click.native="$router.push('/badcommentreceive')"></white-title>
    <white-title text="我给出的差评" :isShow="true" @click.native="$router.push('/badcommentgive')"></white-title>
    <div class="bad-comment-explain">
      <p class="explain__title">差评说明</p>
      <p class="explain__content">1、三颗星以下的评价（不包括三颗星）都是差评;</p>
      <p class="explain__content">2、根据不同的差评等级扣掉卖家一定的召币;</p>
      <p class="explain__content">3、根据不同的差评等级扣掉卖家一定的评价总分，进而影响商家排名;</p>
      <p class="explain__content">3、自差评产生起，卖家有3天的协调时间去让买家删除差评;</p>
      <p class="explain__content">3、若卖家对差评有争议，可以向平台申诉，平台有专业客服进行客观仲裁，经核实后，平台可删除</p>
    </div>
  </div>
</template>

<script>
import defaultHead from '@/components/head/defaultHead'
import whiteTitle from '@/components/common/whiteTitle'

export default {
  data () {
    return {
    }
  },
  components: { defaultHead, whiteTitle }
}
</script>

<style lang="less" scoped>
@import '../assets/css/variable.less';

.bad-comment-page {
  padding-top: .88rem;
  .bad-comment-explain {
    padding: 0 .8rem 0 .22rem;
    .explain__title {
      font-size: .32rem;
      padding-top: .13rem;
      line-height: .84rem;
      color: @defaultColor;
    }
    .explain__content {
      font-size: .28rem;
      line-height: .49rem;
      color: @midleColor;
      text-indent: -.44rem;
      margin-left: .44rem;
    }
  }

}

</style>



