<template lang="html">
  <div class="content">
    <div class="content-title" :style="{margin: margin, marginTop: marginT, padding: padding}">

      <p class="content-title__text" :style="{color: textColor, borderBottom: borderB, borderTop: borderT}"><img v-if="hasImg" :src="hasImg" alt=""><span v-if="isContact">网络查找号召力账号 </span><span :class="{'activeColor': isContact}">{{text}}</span></p>
      <i class="arrow__icon" v-if="isShow"></i>
    </div>
  </div>
</template>

<script>
export default {
  props: {
    text: {
      type: String,
      require: true
    },
    isContact: {
      type: Boolean,
      default: false
    },
    isShow: {
      type: Boolean,
      default: false
    },
    hasImg: {
      type: String,
      default: ''
    },
    marginT: {
      type: String,
      default: '0rem'
    },
    padding: {
      type: String,
      default: '0 .22rem'
    },
    margin: {
      type: String,
      default: '0rem'
    },
    borderB: {
      type: String,
      default: 'none'
    },
    borderT: {
      type: String,
      default: 'none'
    },
    textColor: {
      type: String,
      default: '#333'
    }
  }
}
</script>

<style lang="less" scoped>
@import '../../assets/css/variable.less';

.content {
  position: relative;
  background: @whiteColor;
}
.content-title {
  img {
    width: .3rem;
    height: .3rem;
    position: relative;
    top: .05rem;
    margin-right: .2rem;
  }
}
.content-title__text {
  font-size: .32rem;
  width: 100%;
  height: .88rem;
  line-height: .88rem;
  color: @defaultColor;
  background-color: @whiteColor;
}
.arrow__icon {
  right: .06rem;
  top: .2rem;
}
</style>



